<template>
    <div class="header-main">
        <div class="w">
            <div class="row justify-content-between">
                <div class="logo col col-md-3 " @click="recover" >
                    <img src="../assets/img/logo.png" alt="">
                </div>
                
                <!-- <div class="logo col col-md-6" >
                    <ul id="menu">
                        <li><a href="javascript:void(0)" @click="activeMenu('proj')" id="proj">项目中心</a></li>
                        <li><a href="javascript:void(0)" @click="activeMenu('team')" id="team">团队风采</a></li>
                        <li><a href="javascript:void(0)" @click="activeMenu('culture')" id="culture">企业文化</a></li>
                    </ul>
                </div> -->
            </div>


        </div>

    </div>
</template>

<script>
export default {
    data(){
        return {
            lang:'',
        }
    },
    methods: {

        activeMenu(id) {
            let lis = document.querySelectorAll('ul#menu a');
            lis.forEach(v=>{
                v.classList.remove('a_active');
            })
            let menu = document.getElementById(id);
            menu.classList.add('a_active');
            this.$emit("scroll",id);
        },
        recover(){
        window.scroll(0,0);
        }
    },
    watch:{
       
    }

}
</script>

<style>
.header-main {
    width: 100%;
    height: 70px;
    position: sticky;
    top: 0;
    z-index: 999;
    background-color: #fff;
}

.row {
    margin: 0 auto;
}

.w {
    max-width: 1200px;
    height: 100%;
    margin: 0 auto;
}

.logo img {
    height: 100%;
    padding:5px 0;
}

.logo {
    cursor: pointer;
    height: 70px;
}

#menu {
    display: flex;
    margin: 0;
}

#menu li {
    flex: 1;
    height: 70px;
    line-height: 70px;
    display: block;
    text-align: center;
}

#menu li a {
    font-size: 16px;
    color: #666;
    display: block;
    margin: 0 10px;
    height: 100%;
}

a {
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    -webkit-tap-highlight-color: transparent;
    text-decoration: none;
}

#menu li a:hover {
    color: #498F9F;
    border-bottom: 2px solid #498F9F;
}

.a_active {
    color: #498F9F!important;
    border-bottom: 2px solid #498F9F;
}


</style>